.App {
  background-color: #eeeeee;
  /* min-height: 100%; */
  height: 100%;
}

.page-center {
  background-color: #ffffff;
  box-shadow: 0px 0px 10px #aaaaaa;
}

.caption {
  color: #555555
}

.navigate-bar {
  /* background-color: white; */
  align-items: stretch;
  padding: 0 5px;
}

.navigate-tab {
  padding: 0 10px;
}

.navigate-tab p {
  color: white;
  background-color: #333333;
  padding: 5px 10px;
  margin-bottom: 0;
  box-shadow: 0px 0px 0px 2px #333333;
}

.activate-tab p {
  color: black;
  background-color: white;
  /* box-shadow: 0px 0px 10px #aaaaaa; */ /* shadow style */
  /* clip-path: inset(-10px -10px 0px -10px); */ /* shadow style */
}

.navigate-tab:hover p {
  /* background-color: #eeeeee; */
  position: relative;
  top: -5px;
}

.main {
  /* box-shadow: 0px 0px 10px #aaaaaa; */ /* shadow style */
  padding-top: 30px;
}

.max-width-img {
  max-width: 100%;
}

.whitespace {
  height: 48px;
}

h1 {
  font-family: "Helvetica";
  font-weight: bold;
}

#gototop {
  display: none; 
  position: fixed; 
  background-color: white;
  padding: 10px;
  bottom: 30px; 
  right: 30px;
  z-index: 2; 
  /* font-size: 18px; */
  box-shadow: 0px 0px 10px #aaaaaa;
}

#overlay {
  position: fixed;
  top:0;
  left:0;
  right:0;
  bottom: 0;
  z-index: 90;
  /* width: 100%;
  height: 100%; */
  overflow: hidden;
  background-color: rgba(0,0,0,.8);
  visibility: hidden;
}

#overlay-img {
  max-width: 100%;
  max-height: 100%;    
}
